const localStorage = window.localStorage

export default {
  name: 'storage',

  /**
   *  localStorage
   * @param {string} key 
   * @param {Object} value 
   */
  set (key, value) {
    localStorage.setItem(key, JSON.stringify(value))
  },

  /**
   *  localStorage
   * @param {string} key 
   * @return {Object}
   */
  get (key) {
    return eval('(' + localStorage.getItem(key) + ')' )
  },

  /**
   *  localStorage
   * @param {string} key 
   */
  remove (key) {
    localStorage.removeItem(key)
  }
}


