import Vue from 'vue';
import VueI18n from 'vue-i18n';
import Locales from './locale';
import zhLocale from 'element-ui/src/locale/lang/zh-CN';
import enLocale from 'element-ui/src/locale/lang/en';

Vue.use(VueI18n);

// 
const navLang = navigator.language;
const localLang = (navLang === '' || navLang === 'English') ? navLang : false;
const lang = window.localStorage.getItem('lang') || localLang || '';

Vue.config.lang = lang;
// 
const locales = Locales;
const mergeZH = Object.assign(zhLocale, locales['']);
const mergeEN = Object.assign(enLocale, locales['English']);

Vue.locale('', mergeZH);
Vue.locale('English', mergeEN); 



