                        	{{$t('')}}</span>
                        <div class="listshow">
                            <div class="itemlist">
                                <el-table :data="currentSceneList"  :show-header="false" highlight-current-row @row-contextmenu="onSceneContextMenu" @row-dblclick="dbClickToCallScene">
                                    <el-table-column prop="Name" :label="$t('')"></el-table-column>
