                            <div class="itemlist">
                                <el-table :data="currentTemplateList"  :show-header="false" highlight-current-row @row-contextmenu="onTemplateContextMenu" @row-dblclick="dbClickToCallTemplate">
                                    <el-table-column :label="$t('')">
                                        <template scope="scope">
